import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { TwitterService } from '../../shared/services/twitter/twitter';
import { NoticiasService } from '../../shared/services/noticias/noticias';
import { PresidenteNoticias } from '../noticias/noticias.model';
import { TwitterModel } from '../twitter/twitter.model';

@Component({
    selector: 'app-bolsonaro-page',
    templateUrl: './bolsonaro.component.html',
    styleUrls: ['./bolsonaro.component.scss'],
    animations: [routerTransition()]
})
export class BolsonaroPageComponent implements OnInit {

    countTw = 0;
    countNt = 0;
    wordCloud = '';

    public pieChartLabels: string[] = [
        'Negativo',
        'Positivo'
    ];
    public pieChartData: number[] = [300, 500];
    public pieChartType = 'pie';
    constructor(public twitter: TwitterService,
        public noticias: NoticiasService,
        public modelNews: PresidenteNoticias,
        public twitterModel: TwitterModel) {
        this.searchTwitter();
        this.searchNews();
    }

    searchTwitter() {
        this.wordCloud = 'http://35.237.82.186/app1/wcloud?pres=bolsonaro';
        this.twitter.getSentimento('bolsonaro').then(data => {
            this.pieChartData = [data[0].n, data[1].n];
        });
        this.twitter.getNumMencoes('bolsonaro').then(data => {
            this.countTw = data[0].n;
        });
    }

    searchNews() {
        this.noticias.getMencoes('bolsonaro').then(data => {
            for (let x of data) {
                this.countNt += x.n;
            }
            this.modelNews.nomePresidente = 'Jair Bolsonaro';
        });

        this.noticias.getNoticias('bolsonaro|jair bolsonaro').then(data => {
            this.modelNews.vetorNews = data;
        });

    }

    mapTwitter() {
        this.twitterModel.presidente = 'bolsonaro';
    }

    ngOnInit() { }
}
